@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto:wght@700&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    font-weight: lighter;
    background-color: #30336b;
    color: #dff9fb;
}

.App {
    display: flex;
    flex-direction: column;
    justify-content: start;
    width: 520px;
    min-height: 635px;
    text-align: center;
    margin: 64px auto;
    border-radius: 10px;
    padding-bottom: 32px;
    background: #130f40;
}

h1 {
    margin: 32px 0;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    font-size: 1.75rem;
}

.filter {
    margin-bottom: 32px;
}

.filter .btn-filter {
    padding: 8px 16px;
    border-radius: 50px;
    border: none;
    cursor: pointer;
    outline: none;
    margin: 8px;
    background: linear-gradient(
            90deg,
            rgba(93, 12, 255, 1) 0%,
            rgba(155, 0, 250, 1) 100%
    );
    color: #fff;
    text-transform: capitalize;
}

.todo-list {
    display: flex;
    flex-direction: column;
}

.complete {
    text-decoration: line-through;
    opacity: 0.6;
}

.todo-form {
    margin-bottom: 32px;
}

.todo-form input {
    padding: 14px 32px 14px 16px;
    border-radius: 4px 0 0 4px;
    border: 2px solid #5d0cff;
    outline: none;
    width: 320px;
    background: transparent;
    color: #dff9fb;
}

.todo-form input::placeholder {
    color: #e2e2e2;
}

.todo-form button {
    padding: 16px;
    border: none;
    border-radius: 0 4px 4px 0;
    cursor: pointer;
    outline: none;
    background: linear-gradient(
            90deg,
            rgba(93, 12, 255, 1) 0%,
            rgba(155, 0, 250, 1) 100%
    );
    color: #dff9fb;
    text-transform: capitalize;
}

.todo-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 8px auto;
    color: #dff9fb;
    background: linear-gradient(
            90deg,
            rgba(93, 12, 255, 1) 0%,
            rgba(155, 0, 250, 1) 100%
    );
    padding: 16px;
    border-radius: 5px;
    width: 90%;
}

.check-icon {
    color: #7bed9f;
    font-size: 24px;
    margin: 8px;
}

.delete-icon {
    font-size: 24px;
    cursor: pointer;
    border: none;
    background: transparent;
    color: #dff9fb;
}
